/* Customize your main colors in :root variables */
:root {
  --main-background-color: #344e50;
  --card-background-color: #524c4c98;
  --card-background-lighter-color: #7A7F92;
  --main-text-color:#7ba58f;
  --title-text-color:#3CBA8B;
}

body{
  background-image:url("https://i.ibb.co/cF3PN9Y/background.png");
  background-repeat: no-repeat;
  background-size: cover;
}

h2, p{
  color: var(--main-text-color);
}

h3{
font-weight: normal;
}

.MuiFab-extended{
  height: auto;
  padding-right: 0 !important;
}

.MuiFab-label{
  font-weight: bold;
}

.MuiPaper-root{
  background-color: var(--card-background-color) !important;
  margin-left: 420px
}

.MuiButton-containedPrimary:not(.MuiButton-fullWidth){
  border-radius: 24px !important;
  padding: 6px 16px;
}

.MuiButton-label{
  font-size: 1.2em;
}

.MuiLinearProgress-colorPrimary{
  background-color:var(--main-text-color) !important;
}

.MuiLinearProgress-barColorPrimary{
  background-color:var(--title-text-color) !important;
}
